  text-align: center;
  padding: 12px 0;
  /* background: -webkit-gradient(linear, left top, right top, from(rgba(4,144,214,1)),to(rgba(3,116,172,1)));
  background: linear-gradient(to right, rgba(4,144,214,1) 0%,rgba(3,116,172,1) 100%);  */
  background: #00B4DB;
  background: -webkit-gradient(linear, left top, right top, from(#0083B0), to(#00B4DB));
  background: linear-gradient(to right, #0083B0, #00B4DB); }

