  padding: 12px 0;
  color: #7b7b87;
  text-transform: none; }

.shop--icon > svg {
  height: 28px; }

.shop--nav-labels {
  font-family: 'Montserrat', sans-serif;
  font-size: 14px;
  font-weight: 600;
  padding: 0 26px;
  border-right: 1px solid #6d6868;
  cursor: pointer;
  color: #7b7b87;
  text-transform: uppercase;
  text-decoration: none; }

.shop--wrapper {
  overflow: auto; }

.shop--wrapper_project {
  grid-area: project; }

.shop--wrapper_html {
  grid-area: html; }

.shop--wrapper_css {
  grid-area: css; }

.shop--wrapper_js {
